@import url('https://fonts.googleapis.com/css2?family=Andada&family=Caladea:ital,wght@0,400;0,700;1,400&display=swap');

html {
  font-size: 62.5%;
}

html, body, #root {
  height: 100%;
}

* {
  padding: 0;
  margin: 0;
  border: none;
  box-sizing: border-box;
}

// ArcGIS
.embed-container {
  position: relative;
  padding-bottom: 80%;
  height: 0;
  max-width: 100vw;
}

.embed-container iframe,
.embed-container object,
.embed-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}

small {
  position: absolute;
  z-index: 40;
  bottom: 0;
  margin-bottom: -15px;
}
